import React, { createContext, useEffect, useState, useReducer, useRef, useMemo, useCallback, useLayoutEffect, useContext, Component } from 'react';
import {
    EditorState, ContentState, ContentBlock, CharacterMetadata, SelectionState, convertToRaw, convertFromRaw,
    RichUtils, Modifier, convertFromHTML, AtomicBlockUtils
} from 'draft-js';

import { ThemeProvider, useTheme, createTheme } from '@mui/material/styles';

import { Button, CssBaseline, Switch, Slider } from '@mui/material';
import DraftEditor from './DraftEditor';
import { stateToHTML } from 'draft-js-export-html';


import Immutable from "immutable";


import axios from 'axios';



export const Context = createContext();

export function toPreHtml(editorState, imageObj, imgSnapUrlArr, imgUrlArr) {


   // console.log(imageObj)

    const preHtml = stateToHTML(
        editorState.getCurrentContent(),
        {
            defaultBlockTag: "div",
            inlineStyleFn: function (styleNameSet, ...props) {

                const styleObj = {
                    element: "span",
                    style: {},
                    attributes: {}
                }

                if (styleNameSet.toArray().includes("linkTagOn")) {
                    //  styleObj.style = { color: blue[800] }
                    styleObj.attributes["data-type"] = "link"
                }
                if (styleNameSet.toArray().includes("linkTagOff")) {
                    //    styleObj.style = { color: blue[800] }
                    styleObj.attributes["data-type"] = "link"
                }

                return styleObj

            },


            blockStyleFn: function (block) {

                const text = block.getText()
                const data = block.getData().toObject()
                const type = block.getType()
                const key = block.getKey()



                return {
                    style: {
                        //   ...(type === "centerBlock") && { textAlign: "center" },  // style will be a string not an object during toHtmll call
                        //   ...(type === "rightBlock") && { textAlign: "right" },
                        //   ...(type === "codeBlock") && { backgroundColor: "gray" },


                    },
                    attributes: {
                        "block-type": type,
                        ...(type !== "codeBlock" && data.isSmallFont) && { "font-size": "small" },
                        ...(type === "codeBlock" && data.isSmallFont) && { "font-size": "big" },
                        ...(!data.isSmallFont) && { "font-size": "normal" },

                        // ...(type === "centerBlock") && { "text-align": "center" },
                        // ...(type === "rightBlock") && { "text-align": "right" },
                    }
                }
            },

            blockRenderers: {
                imageBlock: function (block) {
                    const text = block.getText()
                    const data = escape(JSON.stringify(block.getData().toObject()))
                    //  console.log(JSON.stringify(block.getData().toObject()))

                    //const data = block.getData().toObject()

                    const type = block.getType()
                    const key = block.getKey()
                    // console.log(key, imageObj[key])

                    let urlSnapString = ""
                    let urlString = ""
                    imageObj[key].forEach((img, index) => {
                        // console.log(img.imgUrl.substr(img.imgUrl.lastIndexOf("/") + 1))
                        // console.log(img.imgSnap.substr(img.imgSnap.lastIndexOf("/") + 1))
                        console.log(key + index, imgUrlArr)

                        urlSnapString = urlSnapString + imgSnapUrlArr.find(item => item.itemKey === (key + index))?.url + " "

                        urlString = urlString + imgUrlArr.find(item => item.itemKey === (key + index))?.url + " "
                        // urlString = urlSnapString + imgSnapUrlArr.find(item => item.itemKey === (key + index))?.url + " "
                    })
                    //data-image_snap_url="${urlSnapString.trim()}" data-image_url="${urlString.trim()}"                
                    return `<object  data-type="image-block"  data-block_key="${key}" data-block_data="${data}" data-image_snap_url="${urlSnapString.trim()}" data-image_url="${urlSnapString.trim()}" >` + escape(block.getText()) + '</object>'
                },
            }
        }

    )

    return preHtml
}




export function useDraftContext() {

    return useContext(Context)

}

export function useEditorState(savedEditorState) {
    const [editorState, setEditorState] = useState(savedEditorState || EditorState.createEmpty())
    return [editorState, setEditorState]
}


export function ContextProvider({
    savedEditorState,
    setSavedEditorState,
    savedImageObj,
    setSavedImageObj,
    onSubmit,
}) {

    let [editorState, setEditorState] = useEditorState(savedEditorState)

    const [currentBlockKey, setCurrentBlockKey] = useState("ddd")

    const [imageObj, setImageObj] = useState(savedImageObj || {})
    const imageBlockNum = editorState.getCurrentContent().getBlocksAsArray().filter(block => block.getType() === "imageBlock").length


    useEffect(function () {

        setSavedImageObj(imageObj)

    }, [imageObj, imageBlockNum])



    return (

        <Context.Provider value={{
            editorState, setEditorState,
            currentBlockKey, setCurrentBlockKey,
            imageObj, setImageObj, setSavedImageObj,
            imageBlockNum,

            toPreHtml,
            onSubmit,
        }}>
            <DraftEditor />
        </Context.Provider>
    )


}