import logo from './logo.svg';
import './App.css';

import React, { useState } from 'react';

import {
  Container, Grid, Paper, IconButton, ButtonGroup, Stack, Box, Button, Chip, Avatar, CssBaseline, Typography, Collapse, Switch, Divider,
  Slider, TextField
} from '@mui/material';


import ThemeContextProvider from './ThemeContextProvider';
import Content from './Content';

import { ContextProvider as EditorCtx } from "./ContextProvider";

import axios from "axios";
import { hostUrl } from "./config";

function uploadImage(img) {
  let body = new FormData()
  body.set('key', 'e60be67db1a2f95d7dad278bdfa2650c')
  body.append('image', img)

  return axios({
    method: 'post',
    url: 'https://api.imgbb.com/1/upload',
    data: body
  })
}


function App() {


  const [savedImageObj, setSavedImageObj] = useState()

  const [contentArr, setContentArr] = useState([])


  return (
    <ThemeContextProvider>

      <Container disableGutters={true} fixed={false} maxWidth={window.innerWidth >= 3000 ? false : "lg"} sx={theme => ({ bgcolor: theme.pa })}>

        <Grid container
          direction="row"
          justifyContent="space-around"
          alignItems="flex-start"
          spacing={0}
          sx={{}}
        >
          <Grid item xs={10} sm={10} md={10} lg={10} xl={10} sx={{}}>
            <EditorCtx
              savedImageObj={savedImageObj}
              setSavedImageObj={setSavedImageObj}
              onSubmit={function (preHtml) {

                //     console.log(savedImageObj)
                //     console.log(preHtml)

                //     console.log(savedImageObj)
                setContentArr(arr => [{ content: preHtml, createTime: Date.now() }, ...arr])

                console.log(preHtml)
                axios.post(`${hostUrl}/uploadMessage`, {
                  createTime: new Date(),
                  content: preHtml,
                })

              }}


            />
          </Grid>

          <Grid item xs={12} sm={12} md={12} lg={12} xl={12} sx={{}}>


            <Content contentArr={contentArr} setContentArr={setContentArr} />

          </Grid>


        </Grid>
      </Container>

    </ThemeContextProvider >
  );
}

export default App;
