import React, { useEffect, useState, useRef, useMemo, useContext, useCallback } from 'react';
import { EditorState, ContentState, ContentBlock, CharacterMetadata, SelectionState, convertToRaw, convertFromRaw, RichUtils, Modifier, convertFromHTML, AtomicBlockUtils } from 'draft-js';

import { ThemeProvider, useTheme, createTheme } from '@mui/material/styles';

import { Crop, DoneRounded, Close, AddCircleOutline, StackedBarChart, WrapText } from '@mui/icons-material';
import { Container, Grid, Paper, IconButton, ButtonGroup, Stack, Box, Button, Typography, Slider, } from '@mui/material';
import { KeyboardArrowUpIcon } from "@mui/icons-material"

import { Context } from "./ContextProvider";



export default function CodeBlock({ children, lineNum, markingCodeBlock, blockKey, blockData, ...props }) {

    const theme = useTheme()

    const { currentBlockKey, editorState, imageBlockNum, setEditorState } = useContext(Context)

    const [wrap, setWrap] = useState("pre-line")


    return (

        <Box sx={{ position: "relative" }} >

            <IconButton size='small' sx={{
                position: "absolute", zIndex: 100, top: -10, right: 0, transform: `translateX(80%)  scale(${currentBlockKey === blockKey ? 0.8 : 0})`,

            }}
                contentEditable={false} suppressContentEditableWarning={true}
                onClick={function () {
                    setWrap(pre => {

                        return pre === "nowrap" ? "pre-line" : "nowrap"

                    })
                    // markingCodeBlock(blockKey, true)

                }}
            >
                <WrapText fontSize="large" sx={{ "&:hover": { bgcolor: "rgba(255,255,255,0.5)", borderRadius: "1000px" } }} />
            </IconButton>


            <IconButton size='small' sx={{
                position: "absolute", zIndex: 100, top: -10, right: 0, transform: `translateX(140%)  scale(${currentBlockKey === blockKey ? 0.8 : 0})`,

            }}
                contentEditable={false} suppressContentEditableWarning={true}
                onClick={function () {

                    markingCodeBlock(blockKey, true)

                }}
            >
                <Close fontSize="large" sx={{ "&:hover": { bgcolor: "rgba(255,255,255,0.5)", borderRadius: "1000px" } }} />
            </IconButton>




            <Box sx={{
                bgcolor: theme.mode === "light" ? "lightgray" : "darkgray", fontFamily: "monospace, monospace", fontSize: theme.scaleSizeObj(0.85),
                lineHeight: 1.2,
                position: "relative",
                overflow: "auto",
                whiteSpace: wrap,
                wordBreak: "break-all",
                ...(blockData.isSmallFont) && { fontSize: theme.scaleSizeObj(1) },
            }}>

                {children}
            </Box>
        </Box >
    )


}