import React, { createContext, useEffect, useState, useReducer, useRef, useMemo, useCallback, useLayoutEffect, useContext, Component } from 'react';

import parse, { domToReact, attributesToProps } from 'html-react-parser';

import { Container, Grid, Paper, IconButton, ButtonGroup, Stack, Button, Switch, Box, Hidden, Link } from '@mui/material';

import { Crop, DoneRounded, Close, AddCircleOutline, StackedBarChart, WrapText } from '@mui/icons-material';

import { ThemeProvider, useTheme, createTheme, styled, } from '@mui/material/styles';

import { blue, red, grey } from '@mui/material/colors';

import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';
import axios from 'axios';

import { hostUrl } from "./config";


const options = {
    htmlparser2: {
        lowerCaseTags: false // only works on SSR
    },
    replace: (node) => {

        const { attribs, children, type, name, ...props } = node


        if (name === "span" && attribs["data-type"] === "link") {



            return <LinkTag linkAdd={domToReact(children, options)} />
        }

        if (name === "object") {

            //console.log(attribs["data-image_snap_url"].split(" "))
            console.log(attribs["data-image_url"])


            return <Images imageSnapArr={attribs["data-image_snap_url"].split(" ")} imageArr={attribs["data-image_url"].split(" ")} />

            // return <React.Fragment>
            //     {attribs["data-image_snap_url"].split(" ").map((url,index) => {
            //         return <img src={url} width={100} height={100} key={index} />
            //     })
            //     }
            // </React.Fragment>

        }


        if (name === 'div') {


            const fontSize = attribs["font-size"]
            const blockType = attribs["block-type"]

            const nodeProps = attributesToProps(node.attribs)



            //       console.log(nodeProps)

            //console.log(blockType)



            return (
                blockType === "codeBlock"
                    ? <CodeBlock {...{ blockType, fontSize, nodeProps }}>
                        {domToReact(children, options)}
                    </CodeBlock>
                    : <Box sx={theme => ({
                        px: "8px",
                        fontSize: fontSize === "small" ? theme.scaleSizeObj(0.8) : theme.sizeObj,
                        ...(blockType === "unstyled") && { textAlign: "left" },
                        ...(blockType === "centerBlock") && { textAlign: "center" },
                        ...(blockType === "rightBlock") && { textAlign: "right" },


                    })}>{domToReact(children, options)}</Box>

            )


        }
        else if (name === 'i') {

            const nodeProps = attributesToProps(node.attribs)
            return <b {...nodeProps}>{domToReact(children, options)}</b>


        }

    }
};




export default function Content({ contentArr, setContentArr }) {

    useEffect(function () {

        axios.get(`${hostUrl}/getMessage`).then(response => {

            setContentArr(response.data)

        })

    }, [])



    return (

        <>
            {contentArr.map((item, index) => {

                const keyIndex = item.createTime

                const preHtml = item.content
                return (

                    <Box key={keyIndex} sx={theme => ({ marginTop: "10px", bgcolor: theme.palette.action.hover })}>
                        <Box sx={theme => ({ display: "flex", flexDirection: "row-reverse", })}>
                            <IconButton onClick={function () {

                                console.log(keyIndex)
                               axios.delete(`${hostUrl}/${Number.isInteger(keyIndex)?keyIndex:keyIndex}`)

                                setContentArr(pre => {
                                    return pre.filter(item => {
                                        return Number.isInteger(item.createTime)
                                            ? item.createTime !== keyIndex
                                            : Date.parse(item.createTime) !== Date.parse(keyIndex)


                                    })
                                })

                            }}><Close /></IconButton>
                        </Box>
                        {parse(preHtml, options)}

                    </Box>
                )
            })}
        </>

    )


}


function CodeBlock({ children, blockType, fontSize, nodeProps }) {

    const [wrap, setWrap] = useState("pre-line")



    return (

        <Box sx={theme => ({
            display: "flex", flexDirection: "row", lineHeight: 1.1, px: "8px",

            bgcolor: theme.mode === "light" ? "lightgray" : "darkgray",
        })}>

            <Box

                sx={theme => ({
                    flexGrow: 1,
                    fontSize: theme.sizeObj,
                    height: "min-content",
                    ...(blockType === "centerBlock") && { textAlign: "center" },
                    ...(blockType === "rightBlock") && { textAlign: "right" },
                    ...(blockType === "codeBlock") && {
                        fontSize: fontSize === "big" ? theme.sizeObj : theme.scaleSizeObj(0.85),
                        //    bgcolor: theme.mode === "light" ? "lightgray" : "darkgray", 
                        fontFamily: "monospace, monospace",


                        overflowX: "auto",
                        overflowY: "hidden",
                        whiteSpace: wrap,
                        wordBreak: "break-all",

                    },


                    //  fontSize: fontSize === "small" ? theme.scaleSizeObj(0.85) : theme.scaleSizeObj(1)
                })}  {...nodeProps}>

                {children}

            </Box>

            <IconButton
                size="small"
                //  sx={{ height: "10px" }}
                onClick={function () {
                    setWrap(pre => pre === "nowrap" ? "pre-line" : "nowrap")

                }}

            ><WrapText fontSize='small' /></IconButton>
        </Box>
    )

}


function LinkTag({ linkAdd }) {

    const theme = useTheme()

    // console.log(linkAdd.match(/(\/\/)([a-z0-9\-._~%]+)/))
    const [content, setContent] = useState("")

    return (
        <Box
            component="span"
            target="_blank"
            //  key={index}
            sx={{
                color: theme.isLight ? blue[600] : blue[800],
                cursor: "pointer",
                textDecoration: "none",
                "&:hover": { textDecoration: "underline" }
            }}

            onClick={function (e) {

                setContent(
                    <Link href={linkAdd} target="_blank" rel="noopener"
                        sx={{
                            color: theme.isLight ? blue[600] : blue[800],
                            cursor: "pointer",
                            textDecoration: "none",
                            "&:hover": { textDecoration: "underline" }
                        }}
                    >
                        {linkAdd}
                    </Link>

                )
            }}
        >
            {content || linkAdd.match(/(\/\/)([a-z0-9\-._~%]+)/)[2]}
        </Box >
    )
}

function Images({ imageSnapArr, imageArr }) {
    console.log(imageArr)
    const theme = useTheme()
    const frameRef = useRef()
    const numOfImage = imageSnapArr.length
    const [height, setHeight] = useState(1)
    const baseGrid = [
        {
            gridTemplateColumns: "1fr",
            gridTemplateRows: "1fr",
        },
        {
            gridTemplateColumns: "1fr",
            gridTemplateRows: "1fr",
        },
        {
            gridTemplateColumns: "1fr 1fr",
            gridTemplateRows: "1fr",
        },
        {
            gridTemplateColumns: "1fr 1fr 1fr",
            gridTemplateRows: "1fr 1fr",
        },
        {
            gridTemplateColumns: "1fr 1fr",
            gridTemplateRows: "1fr 1fr",
        },
    ][numOfImage]

    const arr = [
        [
            { gridColumn: "1/3", gridRow: "1/3" }
        ],
        [
            { gridColumn: "1/2", gridRow: "1/3" },
            { gridColumn: "2/3", gridRow: "1/3" },
        ],
        [
            { gridColumn: "1/2", gridRow: "1/2" },
            { gridColumn: "2/3", gridRow: "1/2" },
            { gridColumn: "3/4", gridRow: "1/2" },

        ],
        [
            { gridColumn: "1/2", gridRow: "1/2" },
            { gridColumn: "2/3", gridRow: "1/2" },
            { gridColumn: "1/2", gridRow: "2/3" },
            { gridColumn: "2/3", gridRow: "2/3" },
        ]
    ]

    const cssObj = {
        display: 'grid',
        ...baseGrid,
        // gridTemplateColumns: "1fr 1fr",
        // gridTemplateRows: "1fr 1fr",
        gridGap: "2px",
        position: "relative",

        //   marginTop: "2px",
        //  marginBottom: "2px",
        justifyContent: 'space-around',
        overflow: 'hidden',

        width: "100%",

        height: height + "px",
        bgcolor: "skyblue",

        ...numOfImage > 0 && {
            "& > *:nth-type(1)": {
                position: "relative",
                ...arr[numOfImage - 1][0],
            }
        },
        ...numOfImage > 1 && {
            "& > *:nth-type(2)": {
                position: "relative",
                backgroundColor: "#ffa",
                ...arr[numOfImage - 1][1]
            }
        },
        ...numOfImage > 2 && {
            "& > *:nth-type(3)": {
                position: "relative",
                backgroundColor: "#afa",
                ...arr[numOfImage - 1][2]
            }
        },
        ...numOfImage > 3 && {
            "& > *:nth-type(4)": {
                position: "relative",
                backgroundColor: "#aaf",
                ...arr[numOfImage - 1][3]
            }
        },
        ...numOfImage > 4 && {
            "& > *:nth-type( n + 5 )": {
                display: "none",
            }
        },

    }
    const imageHeight = [{ height: 0 }, { height }, { height }, { height }, { height: height / 2 }][numOfImage]

    // useEffect(function () {
    //     const resizeObserver = new ResizeObserver(([element]) => {
    //         //console.log(element.contentRect.width)
    //         // setSize(element.contentRect)
    //         console.log(element.contentRect.width)

    //         setHeight([
    //             1,
    //             element.contentRect.width / 16 * 9,
    //             element.contentRect.width / 2,
    //             element.contentRect.width / 3,
    //             element.contentRect.width / 16 * 9,
    //         ][numOfImage])


    //     })
    //     resizeObserver.observe(frameRef.current)

    //     return function () { resizeObserver.disconnect() }
    // }, [])



    return (
        imageSnapArr.map((item, index) => {



            return <Box key={index} sx={{ lineHeight: 0, px: "0px" }}><a href={item} target="_blank"><img src={item} style={{ objectFit: "cover", }} /></a></Box>


        })

    )
    return (
        <>









            <Box className="image-frame" sx={cssObj} ref={frameRef}>



                {imageSnapArr.map((item, index) => {

                    return <Box style={{
                        // backgroundColor: "pink",
                        width: "100%",
                        ...imageHeight,
                        display: "block",
                        position: "relative"
                    }}

                        key={index}
                    >

                        <img src={item} key={index} style={{ objectFit: "cover", width: "100%", height: "100%" }} />

                    </Box>
                })}
            </Box>
        </>
    )

}

